.skill-container{
    width: 100%;
    background-color: #abc4ff;
    height: 500px;
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.skill-carousel-controller{
    background-color: rgb(28, 89, 146);
    color: white;
    height: 100%;
    width: 40%;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.skill-carousel-controller span{
    font-family: 'Poppins Regular';
}

.skill-carousel-controller-button{
    margin-top: 30px;
}

.skill-carousel-controller-button > *{
    margin: 10px;
}

.btn-fa{
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
}

.fa-arrow-circle-left, .fa-arrow-circle-right{
    color: white;
}

.skill-carousel-container{
    display: flex;
    padding: 20px;
    position: absolute;
    left: 500px;
    right: 0;
}

.skill-carousel-container > *{
    margin: 0px 12px;
}

.skill-carousel-rectangle{
    height: 60vh;
    width: 20vw;
    background-color: white;
}
