.contactme-container{
    margin-bottom: 0;
    background-color: #1f2235;
    height: 200px;
    width: 100%;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contactme-title{
    font-family: 'Poppins Bold';
    color: white;
}

.contactme-title span{
    font-size: 30px;
    font-weight: 100;

}

.contactme-main{
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.contactme-main > *{
    margin-right: 20px;
}

.contactme-main a{
    text-decoration: none;
    color: cyan;
}
