.aboutme-container{
    margin-top: 5%;
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.aboutme-title{
    font-family: 'Poppins Bold';
}

.aboutme-title h1{
    font-size: 40px;
    font-weight: 100;
    padding-bottom: 15px;
    border-bottom-style: solid;
    border-bottom-width: 5px;
    width: fit-content;
    border-radius: 3%;
}

.aboutme-rectangle{
    margin: 50px;
    background-color: rgb(167, 167, 167);
    color: white;
    height: 500px;
    width: 70%;
    box-shadow: 0 5px 12px rgb(0 0 0 / 0.2);
    border-radius: 10px;
    display: flex;
}

.aboutme-photo{
    background-color: brown;
    width: 50%;
    
}

.aboutme-description{
    padding: 10px;
    width: 50%;
    font-family: 'Poppins Regular';

}
