.exp-title{
    font-family: 'Poppins Bold';
    margin-left: auto;
    margin-right: 10%;
}

.exp-title h1{
    font-size: 40px;
    font-weight: 100;
    padding-bottom: 15px;
    border-bottom-style: solid;
    border-bottom-width: 5px;
    width: fit-content;
    border-radius: 3%;
}

.exp-container{
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

.exp-main-container{
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.95)), url('../../assets/experience/bg-1.jpg');
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    padding-bottom: 0px;
    margin-bottom: 0px;
    padding-top: 5%;
}

.exp-content-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 1%;
}

.exp-content{
    margin: 50px;
    background-color: rgb(255, 192, 159);
    height: 90%;
    width: 10%;
    border-radius: 10px;
    box-shadow: 0 5px 12px rgb(211, 211, 211);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

h3{
    font-family: 'Poppins';
}