.profile-container {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.primary-text {
    font-size: 24px;
    font-family: "Poppins SemiBold";
    color: white;
}
.primary-moving-text {
    font-size: 64px;
    font-family: "Poppins SemiBold";
    color: green;
}
.primary-desc-text {
    font-size: 24px;
    font-family: "Poppins Regular";
    color: white;
}

.profile-name {
    color: red;
}