.edu-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding-left: 10%;
    padding-right: 10%;
}

.edu-title{
    font-family: 'Poppins Bold';
    margin-right: auto;
    
}

.edu-title h1{
    font-size: 40px;
    font-weight: 100;
    padding-bottom: 15px;
    border-bottom-style: solid;
    border-bottom-width: 5px;
    width: fit-content;
    border-radius: 3%;
}

.edu-main-container{
    margin-top: 50px;
    padding: 10px;
    background-color: rgb(255, 192, 159);
    height: 150px;
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 5px 12px rgb(0 0 0 / 0.2);
    font-family: 'Poppins';
}

.edu-main-container>a{
    color: red;
}