.home-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.95)), url("../../assets/home/bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 113vh;
}

.bottom-home-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    flex-direction: column;
}

.bottom-text{
    font-size: 10px;
    font-family: "Poppins Regular";
    color: grey;
}

.fa-angle-double-down{
    color: grey;
}